.nav {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    color: inherit;
    text-decoration: none;
    font-size: 22px;
    letter-spacing: 2px;
    position: relative;

    @include linkHoverUnderline;
  }
}
